input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}

label.switchLabel {
  cursor: pointer;
  text-indent: -9999px;
  width: 200px;
  height: 100px;
  background: grey;
  display: block;
  border-radius: 100px;
  position: relative;
}

label.switchLabel:after {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  width: 90px;
  height: 90px;
  background: #fff;
  border-radius: 90px;
  transition: 0.3s;
}

input:checked + label.switchLabel {
  background: #bada55;
}

input:checked + label.switchLabel:after {
  left: calc(100% - 5px);
  transform: translateX(-100%);
}

label.switchLabel:active:after {
  width: 130px;
}
label.switchLabel,
input#switch {
  transform: scale(0.3);
}

.nav {
  height: 10vh;
  padding: 0 50px;
  display: flex;
  background-color: rgb(173, 202, 227);
  align-items: center;
  justify-content: space-between;
}

.nav--container h1 {
  text-transform: uppercase;
  font-size: 24px;
}
.nav--container p {
  margin-top: 5px;
  text-transform: lowercase;
  font-size: 14px;
}
.nav .modetoggle {
  max-width: 130px;
  transform: translateY(-10px);
}
